import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-sort',
  templateUrl: './sort.component.html',
  styleUrls: ['./sort.component.css']
})
export class SortComponent implements OnInit {
  array = [];
  algos = ["BubbleSort", "MergeSort"];
  size = 0;
  selectedAlgo: string;
  speed = ["Slow", "Medium", "Fast"];
  selectedSpeed: string;
  arrayLength = 30;
  speedTime = 0;

  constructor() {
  }

  ngOnInit() {
    this.generateRandomArray();
    this.selectedAlgo = this.algos[0];
    this.selectedSpeed = this.speed[0];
  }

  generateRandomArray() {

    this.array = [];
    this.array = Array.from({ length: this.arrayLength }, () => this.randomIntFromInterval(4, 500));
  }

  getStyle(arr) {
    return arr.toString() + 'px';
  }

  randomIntFromInterval(min, max) {
    return Math.floor(Math.random() * (max - min + 1) + min);
  }

  sizeChanged(val) {
    this.size = val;
    console.log(this.size);

  }

  speedChanged(speed) {
    switch (speed) {
      case "Slow":
        this.speedTime = 250;
        break;
      case "Medium":
        this.speedTime = 0;
        break;
      case "Fast":
        this.speedTime = -250;
        break;


      default:
        break;
    }
  }

  visualize() {
    switch (this.selectedAlgo) {
      case "BubbleSort":
        this.bubbleSort();
        break;

      default:
        break;
    }
  }

  async bubbleSort() {
    let len = this.array.length;
    let swapped;

    do {
      swapped = false;

      for (let i = 0; i < len - 1; i++) {
        document.getElementById('bar_' + i).style.backgroundColor = 'blue';
        document.getElementById('bar_' + (i + 1)).style.backgroundColor = 'blue';

        if (this.array[i] > this.array[i + 1]) {
          let tmp = this.array[i];
          this.array[i] = this.array[i + 1];
          this.array[i + 1] = tmp;
          swapped = true;
        }
        await new Promise(resolve => setTimeout(resolve, this.speedTime));
        document.getElementById('bar_' + i).style.backgroundColor = '#563d7c';
        document.getElementById('bar_' + (i + 1)).style.backgroundColor = '#563d7c';
      }
    } while (swapped);

    return this.array;
  }

  async mergeSort() {

  }

  async quicksort() { }

  async heapsort() { }

}
